import React, { useState, useEffect } from 'react'
import { NavLink } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import {
  CContainer,
  CHeader,
  CHeaderBrand,
  CHeaderDivider,
  CHeaderNav,
  CHeaderToggler,
  CNavLink,
  CNavItem,
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { cilMenu, cilAccountLogout } from '@coreui/icons'

import { AppBreadcrumb } from './index'
import { logo } from 'src/assets/brand/logo'
import { FcBusinessman } from "react-icons/fc";
import { switchSidebarShow, setOptions, setDateOption } from 'src/REDUX/orderSlice';


const AppHeader = () => {
  const dispatch = useDispatch()
  const sidebarShow = useSelector((state) => state.sidebarShow)

  const [userName, setUserName] = useState(false);
  const [userId, setUserId] = useState(false);

  useEffect(() => {
    setUserName(localStorage.getItem('user_name'));
    setUserId( JSON.parse( localStorage.getItem('user_data') ) );
  }, [])

  const Logout = () => {
    localStorage.setItem('user', '');
    dispatch(setOptions([])); // Відправляємо ціни до Redux
    dispatch(setDateOption('')); // Відправляємо ціни до Redux
    window.location.replace("/");
  }

  return (
    <CHeader position="sticky" className="mb-4" style={{ backgroundColor: "#f3f3f3" }}>
      <CContainer fluid>
        <CHeaderToggler
          className="ps-1"
          onClick={() => dispatch(switchSidebarShow(sidebarShow))}
        >
          <CIcon icon={cilMenu} size="lg" />
        </CHeaderToggler>
        <CHeaderBrand className="mx-auto d-md-none" to="/">
          <CIcon icon={logo} height={48} alt="Logo" />
        </CHeaderBrand>
        <CHeaderNav className="d-none d-md-flex me-auto"> 
          <FcBusinessman size={25}/>
          {(userName && userName != null)?  
            <CNavItem>
              <CNavLink to={`/users/update/${userId.id}`} component={NavLink}>
                {userName} ⚙
              </CNavLink>
            </CNavItem>
          : ''}
        </CHeaderNav>
        <CHeaderNav>
        </CHeaderNav>
        <CHeaderNav className="ms-3">
          <CNavItem>
            <CNavLink href="#" onClick={Logout}>
              <CIcon icon={cilAccountLogout} size="lg" /> Вийти
            </CNavLink>
          </CNavItem>
        </CHeaderNav>
      </CContainer>
      <CHeaderDivider />
      <CContainer fluid>
        <AppBreadcrumb />
      </CContainer>
    </CHeader>
  )
}

export default AppHeader
