import React, { useEffect, useState } from 'react'
import { AppContent, AppSidebar, AppFooter, AppHeader } from '../components/index'
import Login from 'src/views/pages/login/Login';
/** Reduxe */
import { useDispatch, useSelector } from 'react-redux';
import { setOptions, setDateOption } from 'src/REDUX/orderSlice';
import AxiosInstance from 'src/components/Axios';

const useAuth = () => {

}

const DefaultLayout = () => {
  const dispatch = useDispatch();
  const [auth, setAuth] = useState(true);
  const user = localStorage.getItem('user');
  const exp = localStorage.getItem('user_exp');
  const dateOption = useSelector(state => state.dateOption);

  useEffect(() => {
    let MyAuth = true;

    if (user && exp) {
      const dateNow = new Date();
      const dateSession = new Date(parseInt(exp) * 1000);
      if (dateNow >= dateSession) {
        MyAuth = false;
        // setAuth(false);
      } else {
        MyAuth = true;
        // setAuth(true);
      };
    } else {
      MyAuth = false;
      // setAuth(false)
    };

    setAuth(MyAuth);

    if (MyAuth && dateOption == '') {
      dispatch(setOptions([]));
      AxiosInstance.get('options', {
        params: {
          pagination: false
        },
        headers: { 'accept': 'application/json' }
      }).then((response) => {
        dispatch(setOptions(response.data)); // Відправляємо ціни до Redux
        dispatch(setDateOption(new Date().toISOString().slice(0, 10))); // Відправляємо ціни до Redux
      });
    };
  }, []);


  if (auth) {
    return (
      <div>
        <AppSidebar />
        <div className="wrapper d-flex flex-column min-vh-100 bg-light">
          <AppHeader />
          <div className="body flex-grow-1 px-3">
            <AppContent  />
          </div>
          <AppFooter />
        </div>
      </div>
    )
  } else {
    return (
      <div>
        <Login />
      </div>
    );
  };
};

export default DefaultLayout;